import { playSongMenuList } from '@/hook/playSongMenuList';
import { computed, defineComponent, onMounted, ref, onBeforeMount } from "vue"; // 格式化时间

import { DateTimeFormat } from "@/utils/date-format.js"; // 格式化播放量

import { formatNumber } from "@/utils/formatNumber.js";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
export default defineComponent({
  name: "songmenudetail",
  components: {},
  props: {
    songmenuinfo: {
      type: Object,
      default: () => {}
    }
  },

  setup(props) {
    const router = useRouter();
    const route = useRoute();
    const store = useStore();
    const headerRef = ref();
    console.log(props.songmenuinfo);
    const profile = computed(() => store.state.login.profile);

    const handleSub = () => {
      if (!profile.value) {
        //用户未登录弹出登录框
        store.commit("login/changelogindialog", true);
      }
    };

    const handleShare = () => {
      if (!profile.value) {
        //用户未登录弹出登录框
        store.commit("login/changelogindialog", true);
      }
    };

    const handleDownload = () => {
      if (!profile.value) {
        //用户未登录弹出登录框
        store.commit("login/changelogindialog", true);
      }
    }; // 跳转到歌单创建者页面


    const imgClick = songmenuinfo => {
      router.push({
        path: "/profilePage",
        query: {
          id: songmenuinfo.userId
        }
      });
    }; // 播放全部


    const playClick = () => {
      playSongMenuList(route.query.id);
    };

    return {
      DateTimeFormat,
      formatNumber,
      headerRef,
      handleSub,
      handleShare,
      handleDownload,
      imgClick,
      playClick
    };
  }

});